*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  
}

.main_div{
  width: 100%;
  height: 100vh;
  background:paleturquoise;
  display:flex;
  flex-direction:row;
  justify-content:center; 
  align-items: center;
  text-align: center;
}

.center_div{
  width: 25%;
  height: 80vh;
  background-color:white;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0, 0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color:rgb(219, 188, 8);
  padding: 10px 0 5px 0;
  font-size: 50px;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0, 0.3);
}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 30px;
  font-weight:600;
  width: 60%;
  border-bottom: 2px solid rgb(219, 188, 8);
  outline: none;
}

button {
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color:rgb(219, 188, 8);
  color: #fff;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0, 0.3);
}

button:hover{
  background-color: #20bf6b;
}

ol {
  margin-top:30px;
}

ol li {
  padding-left: 0px;
  text-align: left;
  font-size: 30px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: rgb(219, 188, 8);
  text-transform: capitalize;
}

.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(219, 188, 8);
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0, 0.3);
  
}

.todo_style .fa-times:hover{
  background-color: crimson;  
}



@media (max-width: 768px){
  .center_div{
    width: 55%;
  }

  .todo_style .fa-times {
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  botton{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;

  }
  
}
